body {
  margin: 0;
  padding: 0;
  font-family: 'Major Mono Display', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #121212;
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  position: relative;
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
  padding: 0 20px;
  box-sizing: border-box;
}

.a-style {
  color: white;
  text-decoration: none;
}
.icon {
    color: white;
    text-decoration: none;
}

.space {
    display: inline;
    margin-right: 0.5rem;
}

.main {
    padding-top: 20px;
}

.tag {
    font-family: monospace;
}
.sec-text {
    color: #AAAAAA;
}

.project-name {
    padding: 0;
    border: none;
    outline: none;
    background: none;
    color: inherit;
    font-family: inherit;
    font-size: 1em;
}

.project-icon {
    padding-right: 0.5em;
    color: #AAAAAA;
    text-decoration: none;
}

.project {
    margin-bottom: 1em;
}
